/* 10. Site Footer
/* ---------------------------------------------------------- */

.site-footer {
  position: relative;
  margin: max(12vmin, 64px) 0 0 0;
  padding-top: 48px;
  padding-bottom: 140px;
  color: #fff;
  background: color-mod(var(--bradcolor) l(-5%));
}

.page-template .site-footer {
  margin-top: 0;
}

.site-footer .inner {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: auto 1fr auto;
  color: rgba(255, 255, 255, 0.7);
  font-size: 1.3rem;
}

.site-footer .copyright a {
